:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --text-black: #22252F;
  --border-gray: #2D333C;
  --gray-400: #E2E3EA;
  --text-gray-light: #7F808A;
  --text-gray: #8E8F9E;
  --bg-soft: #efefef;
  --bg-blue: #e0f2f7;
  --bg-green: #e6ffe6;
  --bg-red: #ffe6e6;
  --bg-hover: #f9f9f9;
  --color-green: #008000;
  --color-red: #eb3423;
}

.bc__form {
  background-color: #E2E3EA;
  border-radius: 16px;
  padding: 32px 16px;
  margin-top: 4px;
}

.bc__form .bc__form-container {
  margin: 0 auto;
  padding: 24px 16px 60px;
  border-radius: 20px;
}

@media screen and (min-width: 601px) {
  .barrier-configurator .bc__form {
    margin-top: 90px;
  }
  .barrier-configurator .bc__form-container {
    width: 90%;
  }
}
